import React from 'react'
import Link from 'next/link'

import Drawer from '@material-ui/core/Drawer';
import IconButton from '@material-ui/core/IconButton';
import { List, ListItem, ListItemText } from '@material-ui/core';

import { makeStyles } from '@material-ui/core/styles';


const useStyles = makeStyles({
  list: {
  }
});

const Nav = () => {
  const classes = useStyles();
  const [state, setState] = React.useState({
    menuOpen:false
  });

  const toggleDrawer = (isOpen) => event => {
    if (event.type === 'keydown' && (event.key === 'Tab' || event.key === 'Shift')) {
      return;
    }

    setState({ ...state, menuOpen: isOpen });
  };
  
  return (
    <nav>
      <ul>
        <li>
            <IconButton onClick={toggleDrawer(true)} aria-label="menu">
              <img src='/static/tree_logo.png' width='35' height='35'></img>
            </IconButton>
        </li>
      </ul>
      <Drawer open={state.menuOpen} onClose={toggleDrawer(false)}>
        <div 
          className={classes.list}
          role="presentation"
        >
          <List component="nav">
            <ListItem button>
              <Link href='/space-view'>
                <ListItemText 
                  primary="Live space view with CSS"
                  secondary="May 23, 2020"
                />
              </Link>
            </ListItem>
            <ListItem button>
              <Link href='/static-react'>
                <ListItemText 
                  primary="How to run static React on Github.io with Next.js"
                  secondary="Dec 11, 2019"
                />
              </Link>
            </ListItem>
          </List>
        </div>
      </Drawer>

      <style jsx global>{`
        body {
          margin: 0;
          font-family: -apple-system, BlinkMacSystemFont, Avenir Next, Avenir,
            Helvetica, sans-serif;
        }
        nav {
          text-align: center;
          background: white;
        }
        ul {
          display: flex;
          justify-content: space-between;
        }
        nav > ul {
          margin: 0;
          box-shadow: 0 4px 12px 0 rgba(0, 0, 0, 0.05);
        }
          /* Mobile Styles */
          @media only screen and (max-width: 400px) {
          nav > ul {
              padding: 8px 16px;
            }
          }

          /* Tablet Styles */
          @media only screen and (min-width: 401px) and (max-width: 960px) {
          nav > ul {
              padding: 8px 32px;
            }
          }

          /* Desktop Styles */
          @media only screen and (min-width: 961px) {
          nav > ul {
              padding: 8px 64px;
            }
          }
        li {
          display: flex;
          padding: 6px 8px;
        }
        a {
          color: #067df7;
          text-decoration: none;
        }
        @import url('https://fonts.googleapis.com/css?family=PT+Serif&display=swap');
          body {
              margin: 0;
              padding: 0;
              text-rendering: optimizeLegibility;
              -webkit-font-smoothing: antialiased;
              color: rgba(0,0,0,0.8);
              position: relative;
              min-height: 100vh;
          }
          pre {
              background: rgba(0, 0, 0, 0.05);
              padding: 20px;
              overflow-x: auto;
          }
          .text-outer {
              justify-content: center;
              display: flex;
              margin-top: 15px;
          }
          .text-inner {
              max-width: 680px;   
              width: 100%;
              min-width: 0;
              font-family: 'PT Serif', Georgia, Cambria, "Times New Roman", Times, serif;
              letter-spacing: -0.004em;
              color: rgba(0, 0, 0, 0.84);
              font-style: normal;
              font-weight: 400;
              margin-top: 2em;
              margin-bottom: -0.46em;
              background: white;
              padding: 0 20px;

          }
          /* Mobile */
          @media only screen and (max-width: 400px) {
              .text-inner {
                  margin: 0 24px;
                  line-height: 1.4;
                  font-size: 18px;
              }        
          }
          /* Tablet */
          @media only screen and (min-width: 401px) and (max-width: 960px) {
              .text-inner {
                  margin: 0 48px;
                  line-height: 1.4;
                  font-size: 18px;
              }
          }
          /* Desktop */
          @media only screen and (min-width: 961px) {
          .text-inner {
                  margin: 0;
                  line-height: 1.6;  
                  font-size: 21px;
              }
          }
      `}</style>
    </nav>
  );
}

export default Nav
